'use client';

import React from 'react';
import { motion } from 'framer-motion';
import { ArrowUpRight, TrendingUp, Users, Calendar, Activity } from 'lucide-react';

// DATA DE CASOS (Más detallada que en el Home)
const caseStudies = [
    {
        id: "dental",
        client: "Luminous Smile",
        specialty: "Diseño de Sonrisa",
        stat: "+$45k USD",
        statLabel: "Facturación Adicional / Mes",
        tags: ["Google Ads", "Landing Page", "CRM"],
        before: "Dependencia 100% de referidos. Sitio web lento.",
        after: "Ecosistema digital completo. 120 carillas vendidas en Q1.",
        image: "https://images.unsplash.com/photo-1606811841689-23dfddce3e95?q=80&w=2600&auto=format&fit=crop",
        graphColor: "#bcb5ff"
    },
    {
        id: "derma",
        client: "DermaCenter",
        specialty: "Dermatología Clínica",
        stat: "150+",
        statLabel: "Pacientes Nuevos / Mes",
        tags: ["Meta Ads", "Video Content", "Automatización"],
        before: "Instagram con muchos likes pero agenda vacía.",
        after: "Agenda llena con lista de espera de 3 semanas.",
        image: "https://images.unsplash.com/photo-1581594693702-fbdc51b2763b?q=80&w=2000&auto=format&fit=crop",
        graphColor: "#e7f1ad"
    },
    {
        id: "surgery",
        client: "Dr. Manuel G.",
        specialty: "Traumatología",
        stat: "9.5x",
        statLabel: "ROAS (Retorno de Inversión)",
        tags: ["SEO Local", "Google Maps", "Reputación"],
        before: "Invisible en búsquedas locales. Pocas reseñas.",
        after: "Top 1 en Google Maps en su ciudad. +45 cirugías.",
        image: "https://images.unsplash.com/photo-1666214280557-f1b5022eb634?q=80&w=2000&auto=format&fit=crop",
        graphColor: "#374e86"
    }
];

const CaseStudiesPage = () => {
    return (
        <main className="bg-[#F8F9FF] min-h-screen pt-32 pb-20 px-4 md:px-12">

            {/* HERO SIMPLE */}
            <div className="max-w-[1200px] mx-auto text-center mb-24">
                <span className="font-sans-body text-xs font-bold uppercase tracking-widest text-[#bcb5ff] mb-6 inline-block px-4 py-2 border border-[#bcb5ff]/30 rounded-full">
                    Resultados Auditados
                </span>
                <h1 className="font-massive font-semibold text-5xl md:text-8xl text-[#374e86] mb-8 tracking-tight">
                    LA EVIDENCIA <br />
                    <span className="text-transparent bg-clip-text bg-gradient-to-r from-[#bcb5ff] to-[#374e86]">CLÍNICA.</span>
                </h1>
                <p className="font-sans-body text-lg text-[#374e86]/70 max-w-2xl mx-auto leading-relaxed">
                    No mostramos capturas de pantalla de "Likes". Mostramos consultorios llenos y facturación incremental.
                </p>
            </div>

            {/* LISTA DE CASOS */}
            <div className="max-w-[1200px] mx-auto flex flex-col gap-20">
                {caseStudies.map((item, index) => (
                    <CaseStudyCard key={index} item={item} index={index} />
                ))}
            </div>

            {/* CTA FINAL */}
            <div className="mt-32 text-center">
                <p className="font-serif-display text-2xl text-[#374e86] mb-8">
                    ¿Quieres ser nuestro próximo caso de éxito?
                </p>
                <a href="/contacto" className="inline-flex items-center gap-3 bg-[#374e86] text-white font-bold font-sans-body py-4 px-10 rounded-full hover:bg-[#bcb5ff] hover:text-[#374e86] transition-all duration-300 shadow-xl shadow-[#374e86]/20">
                    Solicitar Análisis <ArrowUpRight size={20} />
                </a>
            </div>

        </main>
    );
};

const CaseStudyCard = ({ item, index }) => {
    return (
        <motion.div
            initial={{ opacity: 0, y: 50 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true, margin: "-10%" }}
            transition={{ duration: 0.8 }}
            className="group sticky top-32 bg-white rounded-[3rem] p-8 md:p-12 border border-[#374e86]/5 shadow-2xl shadow-[#374e86]/5 overflow-hidden"
        >
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 items-center">

                {/* INFO IZQUIERDA */}
                <div>
                    <div className="flex items-center gap-4 mb-8">
                        <span className="font-massive text-5xl text-[#374e86]/10 font-bold">0{index + 1}</span>
                        <div className="h-[1px] flex-1 bg-[#374e86]/10"></div>
                        <span className="font-sans-body text-xs font-bold uppercase tracking-widest text-[#374e86]/50">
                            {item.client}
                        </span>
                    </div>

                    <h2 className="font-serif-display font-bold text-4xl md:text-5xl text-[#374e86] mb-4">
                        {item.specialty}
                    </h2>

                    {/* STAT DESTACADO */}
                    <div className="py-6 border-y border-[#374e86]/5 my-8">
                        <div className="flex items-baseline gap-2">
                            <span className="font-massive text-6xl md:text-7xl text-[#374e86]" style={{ color: item.graphColor }}>
                                {item.stat}
                            </span>
                            <TrendingUp className="text-[#374e86]/40 mb-2" size={32} />
                        </div>
                        <p className="font-sans-body text-sm font-bold uppercase tracking-wide text-[#374e86]/60">
                            {item.statLabel}
                        </p>
                    </div>

                    {/* DIAGNÓSTICO VS SOLUCIÓN */}
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
                        <div className="bg-[#FFF5F5] p-4 rounded-2xl">
                            <p className="text-xs font-bold uppercase text-red-400 mb-2 flex items-center gap-2">
                                <Activity size={14} /> Antes
                            </p>
                            <p className="text-[#374e86]/80 text-sm leading-relaxed">{item.before}</p>
                        </div>
                        <div className="bg-[#F0FDF4] p-4 rounded-2xl">
                            <p className="text-xs font-bold uppercase text-green-500 mb-2 flex items-center gap-2">
                                <Activity size={14} /> Después
                            </p>
                            <p className="text-[#374e86]/80 text-sm leading-relaxed">{item.after}</p>
                        </div>
                    </div>

                    {/* TAGS */}
                    <div className="flex flex-wrap gap-2">
                        {item.tags.map((tag, i) => (
                            <span key={i} className="px-3 py-1 rounded-full border border-[#374e86]/10 text-[#374e86]/60 text-xs font-bold uppercase bg-[#F8F9FF]">
                                {tag}
                            </span>
                        ))}
                    </div>
                </div>

                {/* IMAGEN DERECHA */}
                <div className="relative h-[400px] lg:h-[500px] rounded-[2rem] overflow-hidden group-hover:shadow-2xl transition-all duration-500">
                    <div className="absolute inset-0 bg-[#374e86]/10 mix-blend-multiply z-10 group-hover:bg-transparent transition-colors duration-500" />
                    <img
                        src={item.image}
                        alt={item.client}
                        className="w-full h-full object-cover scale-105 group-hover:scale-100 transition-transform duration-700"
                    />

                    {/* Floating Badge */}
                    <div className="absolute bottom-6 right-6 z-20 bg-white/90 backdrop-blur px-6 py-3 rounded-full shadow-lg flex items-center gap-3">
                        <div className="w-2 h-2 rounded-full bg-green-500 animate-pulse" />
                        <span className="font-sans-body text-xs font-bold text-[#374e86] uppercase">Caso Verificado</span>
                    </div>
                </div>

            </div>
        </motion.div>
    );
};

export default CaseStudiesPage;